<script>
import bannerBtnGroup from '@components/banner-btn-group'
import qrcodeBox from '@components/qrcode-box'

import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
export default {
    name: 'pc_website',
    components:{
        bannerBtnGroup,
        qrcodeBox
    },
    data(){
        return{
            headerFixed: false,
            windowScrollTop: 0,
            hoveringBox: 0,
            activeIndex: 0
        }
    },
    methods:{
        bannerBtnClick(type){
            switch(type){
                case 'email': 
                    this.toEmail();
                    break;
                case 'contact': 
                    break;
                default: 
                    break;
            }
        },
        toEmail(){
            window.location.href = 'mailto:info@yyyad.com'
        },
        toWeibo(){
            window.open('https://weibo.com/234885158')
        },
        nav(idName){
            if(isFinite(idName)){
                window.scrollTo(0,idName);
            }else{
                let targetDoc = window.document.getElementById(idName)
                window.scrollTo(0,targetDoc.offsetTop - 70);
            }
        },
        enterBox(idx){
            clearTimeout(this.ServiceAmination)
            this.ServiceAmination = setTimeout(()=>{
                this.hoveringBox = idx;
            },300)
        },
        leaveBox(){
            clearTimeout(this.ServiceAmination)
            this.hoveringBox = 0;
        },
        slidePrev(){
            this.swiper && this.swiper.slidePrev();
        },
        slideNext(){
            this.swiper && this.swiper.slideNext();
        }
    },
    mounted(){
        let that = this;
        this.swiper = new Swiper('.swiper-container', {
            direction: 'horizontal',
            resistanceRatio: 0,
            watchSlidesProgress: true,
            loop : true,
            autoplay:true,
            on: {
                transitionStart: function () {
                    // console.log(this.activeIndex);
                    that.activeIndex = this.activeIndex;
                }
            },
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
        });

        window.onscroll = ()=>{
            let serviceTop = document.getElementById('service').offsetTop - 100;
            if(window.scrollY > serviceTop){
                this.headerFixed = true
            }else{
                this.headerFixed = false
            }
            // this.windowScrollTop = 
        }
    },
    beforeDestroy(){
        window.onscroll = null;
    }
}
</script>
